<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_sift">
                    <div class="data_s_t">
                        <span>数据筛选</span>
                    </div>
                    <el-row type='flex' justify="space-around">
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-row>
                                    <el-col :span='7'>
                                        <span>员工姓名：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='username' placeholder='姓名'></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-row>
                                    <el-col :span='7'>
                                        <span>登录账号：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='account_num' placeholder='账号' maxlength="20" show-word-limit></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-row>
                                    <el-col :span='7'>
                                        <span>状态：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-select v-model="status_num" placeholder="正常/冻结">
                                            <el-option label="全部" value=""></el-option>
                                            <el-option label="未冻结" value="1"></el-option>
                                            <el-option label="冻结" value="2"></el-option>
                                        </el-select>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s_c">
                                <el-button type="primary" @click='query()' icon="el-icon-search">查询</el-button>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <div class="data_list">
                    <div class="data_l_s">
                        <span>数据列表</span>
                        <el-button type="primary" @click='add' icon='el-icon-plus'>添加</el-button>
                    </div>
                    <div>
                        <el-table :data="user_list.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border @row-click='handle'>
                            <el-table-column prop="id" label="序号">
                            </el-table-column>
                            <el-table-column prop="realName" label="员工姓名">
                            </el-table-column>
                            <el-table-column prop="userName" label="登录账号">
                            </el-table-column>
                            <el-table-column prop="status" label="状态" :formatter='formats'>
                            </el-table-column>
                            <el-table-column prop="createTime" label="创建时间">
                            </el-table-column>
                            <!-- <el-table-column
      						  prop=""
      						  label="创建人">
      						</el-table-column> -->
                            <el-table-column prop="" label="操作">
                                <template scope='scope'>
                                    <div class="oa">
                                        <span class="d" style='color:#0079FE;' @click.stop='oa(scope.$index,scope.row.id)'>冻结</span>
                                        <span style='cursor:pointer;color:#0079FE;' @click.stop='del(scope.$index,scope.row.id)'><i class="el-icon-delete"></i> 删除</span>
                                    </div>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="total">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '账号管理',
            user_list: [],
            username: '',
            account_num: '',
            status_num: '',
            currentPage: 1,
            pagesize: 20,
            total: 0
        }
    },
    methods: {
        /*查询*/
        query() {
            this.$axios.post('/tomlive/user/selectUserByCondition', this.$qs.stringify({
                userName: this.account_num,
                realName: this.username,
                status: this.status_num
            })).then((res) => {
                this.user_list = res.data.info
                // console.log(res)
                setTimeout(() => {
                    var f = document.querySelectorAll('.oa .d')
                    for (var i = 0; i < this.user_list.length; i++) {
                        if (this.user_list[i].status == 1) {
                            f[i].innerHTML = '冻结'
                        } else {
                            f[i].innerHTML = '取消冻结'
                        }
                    }
                }, 1)
            })
        },
        handle(row, event, column) {
            console.log(row, event, column)
            window.sessionStorage.setItem('account_edit',JSON.stringify(row))
            this.$router.push('/home/account_edit')
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#F5F5F5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        add() {
            this.$router.push('/home/new_account')
        },
        formats(row, column, value) {
            if (value === 1) {
                return '未冻结'
            } else {
                return '冻结'
            }
        },
        handleSizeChange(val) {
            // console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
        },
        /*所有用户列表*/
        user_lists() {
            this.$axios.post('/tomlive/user/selectAllUser').then((res) => {
                this.user_list = res.data.info.reverse()
                this.total = this.user_list.length
                setTimeout(() => {
                    var f = document.querySelectorAll('.oa .d')
                    for (var i = 0; i < this.user_list.length; i++) {
                        if (this.user_list[i].status == 1) {
                            f[i].innerHTML = '冻结'
                        } else {
                            f[i].innerHTML = '取消冻结'
                        }
                    }
                }, 1)
            })
        },
        oa(index, id) {
            if (id == 18) {
                alert('管理员账号不可冻结')
            } else {
                this.$confirm('是否冻结?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then((action) => {
                    console.log(action)
                    if (action === 'confirm') {
                        var f = document.querySelectorAll('.oa .d');
                        this.$axios.post('/tomlive/user/updateUserStatus', this.$qs.stringify({
                            id: id
                        })).then((res) => {
                            // console.log(res)
                            if (res.data.code == 201) {
                                f[index].innerHTML = '取消冻结'
                            } else {
                                f[index].innerHTML = '冻结'
                            }
                            this.user_lists()
                        })
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                })

            }
        },
        del(index, id) {
            if (id == 18) {
                alert('管理员账号不可删除')
            } else {
                this.$confirm('是否删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then((action) => {
                    // console.log(action)
                    if (action === 'confirm') {
                        this.$axios.post('/tomlive/user/deleteUserByPrimaryKey', this.$qs.stringify({
                            id: id
                        })).then((res) => {
                            // console.log(res)
                            this.user_lists()
                        })
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                })
            }
        }
    },
    mounted() {
        this.user_lists()
        /*setTimeout(()=>{
        	var t=document.querySelectorAll('.el-table_1_column_3>div')
        	for(var i=0;i<t.length;i++){
        		console.log(t[i].innerHTML)
        		if(t[i].innerHTML=='admin'){

        		}
        	}
        },100)*/
    }
}
</script>
<style scoped>
.data_sift {
    width: 100%;
    height: 132px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0 0 0;
}

.data_s_t {
    width: 100%;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.data_s_t>span {
    font-size: 14px;
    font-weight: bold;
    margin: 0 0 0 20px;
    color: #666;
}

.data_s_c {
    width: 100%;
    height: 35px;
    margin: 20px 0 0 0;
    font-size: 14px;
    color: #666;
    line-height: 35px;
}

.data_list {
    width: 100%;
    min-height: 800px;
    margin: 20px 0 0 0;
    padding: 0px 20px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
}

.data_l_s {
    width: 100%;
    height: 60px;
    font-size: 14px;
    line-height: 60px;
    color: #666;
    font-weight: bold;
}

.data_l_s>button {
    float: right;
    margin: 10px 0;
}

.oa {
    width: 100%;
}

.oa>span {
    margin: 0 10px;
    cursor: pointer;
}

.pages {
    margin: 20px 0 20px 0;
    float: right;
}
</style>